<script>
  import GoogleMapsApiLoader from "google-maps-api-loader";

  export default {


    data() {
      return {
        sheet: false,
        //snackbar: true,

        google: null,
        map: null,
        marker: null,

        mapConfig: {
          center: {lat: 23.6850, lng: 90.3563},
          zoom: 9,
          options: {
            gestureHandling: 'greedy'
          }
        },
        currentPosition: null,
        // apiKey:'AIzaSyDByae0ial2bfrvW-mVOGAoZ2_IrCKpz-M'
        apiKey:'AIzaSyBdudQyn0ECon1ggxM-i3t4xhbQTVYAgLA'
      };
    },

    async mounted() {
      const googleMapApi = await GoogleMapsApiLoader({
        apiKey: this.apiKey,
      });
      this.google = googleMapApi;
      this.initializeMap();
    },

    methods: {
      printPosition(position){
        // this.map.setZoom(8);
        // this.map.setCenter(marker.getPosition());
        console.log(position.latLng.lat(),position.latLng.lng());
        this.currentPosition={
          lat: position.latLng.lat(),
          lng: position.latLng.lng()
        };
        //this.snackbar=true;
        this.sheet=true;
      },
      mapClicked(position){
        let clickedPosition = {
          lat: position.latLng.lat(),
          lng: position.latLng.lng()
        };
        console.log(clickedPosition);
        let marker = new this.google.maps.Marker({
          position: clickedPosition,
          map: this.map,
        });

      },
      mapDragEnded(){
        console.log("Map dragged and bounds changed");
        console.log(this.map.getBounds());
      },
      mapZoomChanged(){
        console.log("Zoom changed");
        console.log(this.map.getBounds());
      },
      initializeMap() {
        const mapContainer = this.$refs.googleMap;
        this.map = new this.google.maps.Map(mapContainer, this.mapConfig);
        let marker = new this.google.maps.Marker({
          position:{
            lat: 24.5,
            lng: 91
          },
          map: this.map,
        });


        marker.addListener('click', this.printPosition);
        this.map.addListener('click',this.mapClicked);
        this.map.addListener('dragend',this.mapDragEnded);
        this.map.addListener('zoom_changed', this.mapZoomChanged);
      }


    }
  };
</script>

